@mixin header-logo-size($size) {
  height: $size;
  width: $size;
}

@mixin square-star-size($size) {
  height: $size;
  width: $size;
}

@mixin font-size-12-500 {
  font-size: 12px;
  font-weight: 500;
}

@mixin font-size-10-400 {
  font-size: 10px;
  font-weight: 400;
}

@mixin font-size-12-400 {
  font-size: 12px;
  font-weight: 400;
}

@mixin font-size-16-700 {
  font-size: 16px;
  font-weight: 700;
}

@mixin font-size-14-700 {
  font-size: 14px;
  font-weight: 700;
}
